<template>
<div class="navbar">
  <a-row>
    <a-col :span="18">
      <div class="logo">
        <img src="../assets/logo.png"/>
      </div>
    </a-col>
    <a-col class="nav" :span="6">
      <a-select class="questionTypeSele" :defaultValue="questionType" @change="handleChange">
      <a-select-option value="l">长句</a-select-option>
      <a-select-option value="m">中句</a-select-option>
      <a-select-option value="s">短句</a-select-option>
    </a-select>
    <a class="judgeA" @click="showModal">评分标准</a>
    <a-modal
        title="评分标准"
        v-model="judgeVisible"
        @ok="handleOk"
      >
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </a-modal>
    </a-col>
  </a-row>
</div>
</template>
<style>
  .judgeA{
    color:#eee;
    margin-left:10px;
  }
  .judgeA:hover{
    color:#fff;
  }
  .questionTypeSele [role=combobox]{
    background:none;
    border:none;
    border-radius: 0;
    color:#eee;
  }
  .questionTypeSele [role=combobox]:active{
    border:0!important;
  }
  .questionTypeSele span{
    color:#fff!important;
  }
</style>
<script>
export default {
  name: 'practiceNav',
  data () {
    return {
      judgeVisible: false,
      questionType: localStorage.getItem('questionType') || 'm'
    }
  },
  methods: {
    showModal () {
      this.judgeVisible = true
    },
    handleOk (e) {
      this.judgeVisible = false
    },
    handleChange (value) {
      localStorage.setItem('questionType', value)
      this.$router.push({
        path: '/Practice/'
      })
    }
  }
}
</script>
